import {Component, OnDestroy, OnInit} from '@angular/core';
import {Subscription} from "rxjs";
import {tap} from "rxjs/operators";
import {FileUploadService} from "../../services/file-upload.service";
import {ClientService} from "../../../../shared/services/client.service";

@Component({
  selector: 'app-wireless',
  templateUrl: './wireless.component.html',
  styleUrls: ['./wireless.component.scss']
})
export class WirelessComponent implements OnInit, OnDestroy{
  title = 'lomobsite';
  public sub: Subscription = new Subscription()
  public viewTemplate: string = 'main';
  public idKiosk = '';

  constructor(public uploadService:FileUploadService,
              private clintService: ClientService
              ) { }


  ngOnInit(): void {
    this.getIdKiosk()
  }

  public changeView(route: string): void {
    this.viewTemplate = route;
    if (route === 'status') {
      this.upload()
    }
  }

  private getIdKiosk() {
    this.idKiosk = this.clintService.currentDevice.identity;
  }

  public upload():void{
    const uploadImage = this.uploadService.uploadPhoto(this.uploadService.gallery, this.idKiosk)
      .pipe(
        tap(() => this.viewTemplate = 'loading')
      )
      .subscribe(
        () => {
          this.viewTemplate = 'success'
          this.uploadService.gallery = [];
        },
        () => {
          this.viewTemplate = 'error'
        }
      );
    this.sub.add(uploadImage);
  }

  ngOnDestroy(): void {
    this.sub.unsubscribe()
  }
}
