import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { WebcamImage } from 'ngx-webcam';
import { BrokerService } from 'src/app/shared/services/broker.service';
import { CameraService } from 'src/app/shared/services/camera.service';
import { ClientService } from 'src/app/shared/services/client.service';
import { MocksService } from 'src/app/shared/services/mocks.service';
import { NavigationService } from 'src/app/shared/services/navigation.service';
import { Location } from '@angular/common';
import { ActivityMode } from 'src/app/core';
import * as moment from 'moment';

@Component({
  selector: 'app-neural-page',
  templateUrl: './neural.component.html',
  styleUrls: ['./neural.component.scss']
})
export class NeuralComponent implements OnInit {

  @Output()
  public photoForSearch: EventEmitter<any> = new EventEmitter<any>();///
  public takePhoto: EventEmitter<any> = new EventEmitter<any>();
  public selectedLocations: string = this.clientService.location || 'Выберите локацию';
  public isLocation: boolean = this.clientService.location ? true : false;
  public selectedDate: any = this.clientService.date;
  public selectedDateTitle: string = 'Выберите дату';
  public webcamImage: WebcamImage | null = null;
  public isValidationLocation: boolean = true;
  public isValidationDate: boolean = true;
  public blur: boolean = false;
  public timeLeft: number = 3
  public flash: boolean = false;
  public blur_white: boolean = false;
  public captureImageData: boolean = false
  handleImage(webcamImage: WebcamImage) {
    this.webcamImage = webcamImage;
  }

  constructor(private router: Router,
    public clientService: ClientService,
    private cameraService: CameraService,
    private mocksService: MocksService,
    private brokerService: BrokerService,
    public navigate: NavigationService,
    private location: Location) { }

  ngOnInit(): void {
    ActivityMode.startTimer();

    this.clientService.neiron.subscribe(item => {
      this.clientService.certainFacesSearch({
        location: 'DC',
        limit: 15,
        offset: 0,
        sampleImage: this.cameraService.photo_search,
        date: moment().format('YYYY-MM-DD'),
      })
      .subscribe(() => {
        this.clientService.getClient().subscribe((item) => {
          this.cameraService.setRotateCamera(item.devices.webCamera.rotate);
          this.brokerService.priceForPaper = (item.products[0].costPenny / 100)
          // this.brokerService.priceForPaper = 1
          this.brokerService.priceForEmail = (item.products[1].costPenny / 100)
        })
    
        this.clientService.standBy().subscribe((item) => {
        })
      });
    });

    this.navigate.clearURL()

  }

  public startTimer() {
    setInterval(() => {
      if (this.timeLeft > 0) {
        this.timeLeft--;
        if (this.timeLeft == 0) {
          this.flash = true
          this.blur_white = true
          // this.photoForSearch.emit({
          //   sampleImage: this.webcamImage?.imageAsDataUrl!
          // });
          this.takePhoto.emit({
            sampleImage: this.webcamImage?.imageAsDataUrl!
          });
          // this.blur = false
          setTimeout(() => {
            this.router.navigate(['gallery'])  
          }, 1000);
          
        }
      }
    }, 1000)
  }

  public navigateTo(rout: string): void {
    this.navigate.navigate(rout);
  }

  public goGallery() { 
    if (!this.isLocation) {
      this.isValidationLocation = false;      
    }
    /*if (!this.selectedDate) {
      this.isValidationDate = false;  
    } */   
    if (!this.isValidationLocation || !this.isValidationDate) {
      return;
    }    
    this.blur = true;
    this.startTimer();
  }
}
