import { AfterViewInit, Component, EventEmitter, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as moment from 'moment';
import { WebcamImage } from 'ngx-webcam';
import { interval } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { PhotoSearchInterface } from 'src/app/shared/interfaces/photoSearch.interface';
import { CameraService } from 'src/app/shared/services/camera.service';
import { ClientService } from 'src/app/shared/services/client.service';
import { NavigationService } from 'src/app/shared/services/navigation.service';
import { SessionService } from 'src/app/shared/services/session.service';

@Component({
  selector: 'app-main-page',
  templateUrl: './main-page.component.html',
  styleUrls: ['./main-page.component.scss']
})
export class MainPageComponent implements OnInit {
  public photoForSearch: PhotoSearchInterface = {
    location: this.clientService.locationID,
    limit: 15,
    offset: 0,
    date: moment().format('YYYY-MM-DD'),
  }
  public webcamImage: WebcamImage | null = null;
  public takePhoto: EventEmitter<boolean> = new EventEmitter<boolean>();
  public isMakePhotoProcess: boolean = false;
  public flash: boolean = false;
  constructor(
    private router: Router,
    private clientService: ClientService,
    private cameraService: CameraService,
    private sessionService: SessionService,
    public navigate: NavigationService
  ) { }

  

  ngOnInit(): void {
    this.navigate.clearURL();
    this.isMakePhotoProcess = false;
    this.cameraService.setRotateCamera(270);
    this.clientService.standBy().subscribe((item) => {
      this.cameraService.setRotateCamera(item.body.devices.webCamera.rotate);
    })    
  }

  /*ngAfterViewInit() {
    this.cameraService.setRotateCamera(270)
  }*/

  onPictureTaken(webcamImage: WebcamImage) {
    this.webcamImage = webcamImage;
    if (this.isMakePhotoProcess) {
      this.cameraService.photo_search = this.webcamImage?.imageAsDataUrl;
      this.sessionService.branch = 'Neural'
      this.clientService.neiron.next(true);
      this.router.navigate(['gallery']);
    }
  }  

  public onMakeSelfie() {
    this.sessionService.branch = 'Selfie';
    this.cameraService.timeFlash = 5;
    this.router.navigate(['selfie']);
  }

  public onFindPhotos() {
    this.sessionService.branch = 'Neural';    
    this.cameraService.timeFlash = 2;
    this.router.navigate(['selfie']);
  }
}
