import { Component, OnInit } from '@angular/core';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient} from '@angular/common/http';
import { MocksService } from 'src/app/shared/services/mocks.service';
import { Router } from '@angular/router';
import { NavigationService } from 'src/app/shared/services/navigation.service';
import { SessionService } from 'src/app/shared/services/session.service';
import { DownloadService } from 'src/app/modules/download-module/download/download.service';
import { ActivityMode } from 'src/app/core';

@Component({
  selector: 'app-main-page',
  templateUrl: './main-page.component.html',
  styleUrls: ['./main-page.component.scss']
})
export class MainPageComponent implements OnInit {
  public slides = this.mocks.getMockMassive();

  public slideConfig = {
    autoplay: false,
    vertical: false,
    verticalSwiping: false,
    slidesToShow: 3,
    slidesToScroll: 1,
    centerMode: false,
    arrows: false,
    infinite: false,
  };

  constructor(private http: HttpClient,
              private mocks:MocksService,
              private navigate:NavigationService,
              private sessionService:SessionService,
              private downloadService:DownloadService) { }

  ngOnInit(): void {
    ActivityMode.stopTimer();
    this.navigate.clearURL();
  }

  public navigateTo(rout:string,uploadMethod?:string):void{
    switch(rout){
      case 'selfie':{
        this.sessionService.branch = 'Selfie';
        // this.navigate.navigate('neural')
        break
      }
      case 'neural':{
        this.sessionService.branch = 'Neural';
        // this.navigate.navigate('neural')
        break
      }

      case 'download':{
        if(uploadMethod == 'smartphone'){
          this.downloadService.uploadMethod = 'smartphone'
        } if(uploadMethod == 'social'){
          this.downloadService.uploadMethod = 'social'
        }
        break
      }
    }
    this.navigate.navigate(rout);
  }
}
