import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {BehaviorSubject, Observable, ReplaySubject} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class NavigationService {
  private _next$: ReplaySubject<any> = new ReplaySubject<any>();
  private _back$: ReplaySubject<any> = new ReplaySubject<any>();
  private _error$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(null);
  public photoForSearch;
  public prevPage:string;

  constructor(private router: Router) { }

  get prev$() {
    return this._back$.asObservable();
  }

  get back(): any {
    return this._back$;
  }

  get back$(): Observable<any> {
    return this._back$.asObservable();
  }

  set back(value: any) {
    this._back$.next(value);
  }

  get next$() {
    return this._next$.asObservable();
  }

  get errorPage$(): Observable<any> {
    return this._error$.asObservable()
  }
  get errorPage() {
    return this._error$.getValue();
  }
  set errorPage(value: any) {
    this._error$.next(value);
  }

  public navigate(rout:string):void{
    this.router.navigate([rout])
  }

  public clearURL():void{ 
    let url: string = this.router.url.substring(0, this.router.url.indexOf("?"));
    if (window.history.replaceState) {
      window.history.replaceState('statedata', 'title', url);
    }
  }
}
