import {Injectable} from '@angular/core';
import {HttpEvent, HttpHandler, HttpInterceptor, HttpRequest} from '@angular/common/http';

import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import { Configuration } from '../modules/outer-config/config';
import { RoutesRecognized } from '@angular/router';
import { Routes } from '../core/handlers/routes';


@Injectable()
export class HostInterceptor implements HttpInterceptor {
  constructor() {
  }

  public intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (request.url.includes('i18n') || (request.url.includes(`${Configuration.host}api/client`))) {
      return next.handle(request);
    }

    const options: { url?: string, setHeaders?: any } = {};

    //let path = Routes[request.url];

    options.url = `${Configuration.host}${request.url}`;

    return next.handle(request);
  }


}
