import { Component, OnInit } from '@angular/core';
import { NavigationService } from 'src/app/shared/services/navigation.service';
import { SmartphoneService } from '../../smartphone/smartphone.service';
import { SocialService } from '../../social/social.service';
import { DownloadService } from '../download.service';

@Component({
  selector: 'app-download',
  templateUrl: './download.component.html',
  styleUrls: ['./download.component.scss']
})
export class DownloadComponent implements OnInit{

  public upload: UploadEnum;
  public header:boolean = true;
  public title:string;

  constructor(private navigate: NavigationService,
              private downloadService:DownloadService,
              private smartphoneService:SmartphoneService,
              private socialService:SocialService) { }

  ngOnInit(): void {
    switch (this.downloadService.uploadMethod) {
      case 'smartphone':
        this.upload = UploadEnum.Smartphone
        break;
      case 'social':
        this.upload = UploadEnum.Social
        break;
    }
    this.navigate.clearURL()
  }

  public navigateTo(rout: string,data?:string, prev?:string): void {
    this.navigate.prevPage = prev
    this.smartphoneService.uploadMethod = data
    this.socialService.socialMethod = data
    this.navigate.navigate(rout);
  }

}

export enum UploadEnum {
  Smartphone = 'smartphone',
  Social = 'social'
}
