import { ImageStatusEnum } from "src/app/core/interfaces/image-status.enum";
import { Configuration } from "src/app/modules/outer-config/config";
import { GalleryInterface } from "../interfaces/gallery.interface";
import { PhotoInterface } from "../interfaces/photo.interface";

export class PhotoMapper {

  public static formatToLocal(data: PhotoInterface[]): any {
    let total = 0;

    data.map((item) => {
      total += Number(item.count || 0);
    });

    return {
      data,
      total
    };
  }
  public static formatImageToLocal(data: GalleryInterface): GalleryInterface {    
    data.statusIsNoFace = data.status === ImageStatusEnum.NoFace || !data.status;     
    data.images.map((item: any) => {
      item.sumOfEmail = 0;
      item.sumOfPaper = 0;
      item.isChecked = false;            
    });
    return data;
  }
}
