import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Observable} from "rxjs";
import { Configuration } from 'src/app/modules/outer-config/config';


@Injectable({
  providedIn: 'root'
})
export class BrokerService {

  constructor(private http: HttpClient) { }

  public sumForPay:number;
  public priceForPaper:number;
  public priceForEmail:number;

  public acquiring(item:any):Observable<any> {
    return this.http.post<any>(`${Configuration.host}api/broker/acquiring`,  JSON.stringify(item), {
      headers: {
        'Content-Type': 'application/json'
      },
      observe: "response"
    });
  }
}
