import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { PhotoSearchInterface } from '../interfaces/photoSearch.interface';


@Injectable({
  providedIn: 'root'
})
export class CameraService {
  public _rotate: number = 0;
  public photo_search:string;
  private _rotateClass: string;  
  public timeFlash: number = 5;

  constructor() {
  }

  private checkRotate = {
    0: 'rotate-top', 
    90: 'rotate-right',
    180: 'rotate-bottom',
    270: 'rotate-left'
  }

  get cssClass() {      
    return this._rotateClass;
  }

  setRotateCamera(rotate?: number): void {
    this._rotate = rotate;
    this._rotateClass = this.checkRotate[this._rotate];    
  }
}

