import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable, ReplaySubject} from 'rxjs';

import {map} from 'rxjs/operators';
import { ImageInterface } from '../interfaces/image.interface';
import { PhotoInterface } from '../interfaces/photo.interface';
import { PhotoMapper } from '../mappers/photo.mapper';


@Injectable({
  providedIn: 'root'
})
export class PhotoService {
  private _photo$: ReplaySubject<ImageInterface[]> = new ReplaySubject<ImageInterface[]>(1);
  public selfiBase64:any[]=[];
  public selfieMas:any[] = [];

  constructor(private http: HttpClient) {
  }

  get photo$(): Observable<any[]> {
    return this._photo$.asObservable();
  }

  set photo(value: any) {
    this._photo$.next(value);
  }


  public getImages(): Observable<PhotoInterface[]> {
    return this.http.get<PhotoInterface[]>('./assets/mocks/photos.json')
      .pipe(
        map(items => PhotoMapper.formatToLocal(items))
      );
  }
}
