import {Injectable, Renderer2, RendererFactory2} from '@angular/core';
import {Observable, ReplaySubject} from 'rxjs';
;
import * as moment from "moment";
import { SessionInterface } from 'src/app/modules/outer-config/config';

@Injectable({
  providedIn: 'root'
})
export class SessionService {
  private _sessionData: SessionInterface | any = null;
  private _session: ReplaySubject<SessionInterface> = new ReplaySubject<SessionInterface>();
  private renderer: Renderer2;
  private _activity: ReplaySubject<any> = new ReplaySubject<any>();
  public branch:string = 'Neural';

  constructor(rendererFactory: RendererFactory2) {
    this.renderer = rendererFactory.createRenderer(null, null);
    this.renderer.listen('window', 'click', (e: any) => {
      if (this._sessionData) {
        const data = this._sessionData;
        this.activity = {
          ...data,
          expiryDate: this.expiryDate
        };
      } else {
        this.activity = {
          date: moment(),
          expiryDate: this.expiryDate
        };
      }
    });
  }

  get activity$(): any {
    return this._activity.asObservable();
  }

  set activity(value: any) {
    this._sessionData = value;
    this._activity.next(value);
  }

  get expiryDate(): moment.Moment {
    return moment().add(120, 'seconds');
  }

  get session$(): any {
    return this._session.asObservable();
  }

  set session(value: any) {
    this._session.next(value);
  }

  // public continueSession() {
  //   this._sessionData.expiryDate = moment();
  // }
}
