import { Component, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { LocationService } from 'src/app/core/services/location.service';
import { ClientService } from '../../services/client.service';
import { NavigationService } from '../../services/navigation.service';
import {LocationInterface} from '../../../core/interfaces/location.interface';

@Component({
  selector: 'app-location',
  templateUrl: './location.component.html',
  styleUrls: ['./location.component.scss']
})
export class LocationComponent implements OnInit {
  public locations$: Observable<LocationInterface[]>;

  @Output() 
  public selectedLoc:any;

  constructor(private router: Router,
              private clientServ:ClientService,
              private locationService: LocationService,
              private navigateService:NavigationService) { }

  ngOnInit(): void {
    this.navigateService.clearURL()
    this.locations$ = this.locationService.getLocations();
  }

  public onSelectLocation(location:LocationInterface):any{
    this.clientServ.Location = location;
    //Deprecated
    this.clientServ.location = location.location;
    this.clientServ.locationID = location.id;
  }

  public navigateTo(rout:string): void {
    this.navigateService.navigate(rout);
  }

}
