import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { Router } from '@angular/router';
import * as moment from 'moment';
import { Observable, Observer } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { ActivityMode } from 'src/app/core';
import { ImageStatusEnum } from 'src/app/core/interfaces/image-status.enum';
import { GalleryInterface } from 'src/app/shared/interfaces/gallery.interface';
import { ImageInterface } from 'src/app/shared/interfaces/image.interface';
import { PhotoSearchInterface } from 'src/app/shared/interfaces/photoSearch.interface';
import { ProductInterface } from 'src/app/shared/interfaces/product.interface';
import { PhotoMapper } from 'src/app/shared/mappers/photo.mapper';
import { BrokerService } from 'src/app/shared/services/broker.service';
import { CameraService } from 'src/app/shared/services/camera.service';
import { ClientService } from 'src/app/shared/services/client.service';
import { MocksService } from 'src/app/shared/services/mocks.service';
import { NavigationService } from 'src/app/shared/services/navigation.service';
import { PhotoService } from 'src/app/shared/services/photo.service';
import { SessionService } from 'src/app/shared/services/session.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.scss']
})
export class GalleryComponent implements OnInit {
  @Output() public changeGallery: EventEmitter<any[]> = new EventEmitter<any[]>();
  @Input() public products: ProductInterface[];
  public activeSlide: any;
  public load: boolean = true;
  public loadTitle: boolean = true;
  public branch: string = '';
  public seen_search_photo:boolean = true
  public isStatusDone: boolean;
  public isStatus: string;
  public isLoaded: boolean;
  private isGalleryLoading: boolean;
  private queryNumber: string;
  private maxCountRequest: number = 20;
  public gallerylist: any = {
    images: []
  };
  public slideConfig: any = {
    autoplay: false,
    vertical: true,
    infinite: false,
    arrows: false,
    verticalSwiping: true,
    centerMode: false,
    slidesToShow: 4,
    slidesToScroll: 3,
    centerPadding: '0px',
    cssEase: 'ease',
    edgeFriction: 0.15,
  };
  public isVertical: boolean = true
  public gallery: ImageInterface[] = [];
  public photoGallery$: Observable<GalleryInterface>;
  public deactivateActivateButtons: string;
  public temporaryItemSliderHeight: number = 1176;
  public activePhotoCount: number = 0;

  public photoForSearch: PhotoSearchInterface = {
    location: this.clientService.locationID,
    limit: 15,
    offset: 0,
    date: moment().format('YYYY-MM-DD'),
  }

  public selfiFromGallery: ImageInterface[] = []
  public selfiMas: string[] = [];
  public selfi = {
    images: this.selfiMas
  }

  public main: any;

  name = "Angular";
  base64TrimmedURL: string;
  base64DefaultURL: string;
  generatedImage: string;
  windowOPen: boolean;

  constructor(private router: Router,
    private clientService: ClientService,
    private mocks: MocksService,
    public cameraService: CameraService,
    private sanitizer: DomSanitizer,
    private sessionService: SessionService,
    private navigate: NavigationService,
    private brokerService: BrokerService, 
    private photoService: PhotoService) {
    this.windowOPen = false;        
    this.clientService.photoToCart = [];
  }


  ngOnInit(): void {
    ActivityMode.startTimer();
    this.photoForSearch.sampleImage = this.cameraService.photo_search;
    this.branch = this.sessionService.branch;    
    switch (this.branch) {
      case 'Selfie': {
        this.cameraService.setRotateCamera(270);
        this.isLoaded = false;
        this.selfiMas = this.photoService.selfieMas
        this.selfi.images = this.photoService.selfieMas
        let key = moment().unix();
        this.photoService.selfiBase64.forEach((image, index) => {
          if (index >3) {
            return;
          } 
          this.selfiFromGallery.push({
            path: image,
            brandId: 1,
            timeStamp: key + index,
            originalUri: '',
            sumOfEmail: 0,
            sumOfPaper: 0,
            isChecked: false,            
          }) 
        });        
        this.getImageWithoutWindowOpen(this.photoService.selfiBase64[0])
        this.getImageWithoutWindowOpen(this.photoService.selfiBase64[1])
        setTimeout(()=> {
          this.isLoaded = true;
        }, 0);
        break
      }
      case 'Neural': {
        this.clientService.getClient().subscribe((item) => {
          this.cameraService.setRotateCamera(item.devices.webCamera.rotate);
          this.brokerService.priceForPaper = (item.products[0].costPenny / 100)
          // this.brokerService.priceForPaper = 1
          this.brokerService.priceForEmail = (item.products[1].costPenny / 100)
        })
    
        this.clientService.standBy().subscribe((item) => {
        })
        
        if(this.navigate.photoForSearch == 'photoBot'){
          this.photoForSearch = null
        }        
        this.cameraService.setRotateCamera(270);
        this.updateImages();
        let requestImages = setInterval(() => {
          if (this.isStatusDone || this.maxCountRequest <=0) {
            clearInterval(requestImages);
            return;
          }
          this.updateImages();
        }, 1000);       
        this.getImageWithoutWindowOpen(this.photoForSearch.sampleImage);
        break
      }
    }

    this.navigate.clearURL()
  }

  public updateImages() {
    if (this.isGalleryLoading) {
      return;
    }
    this.isGalleryLoading = true;   
    if (this.queryNumber) {
      this.photoForSearch.queryNumber = this.queryNumber;
    }
    this.maxCountRequest--;
    this.photoForSearch.location = 'ED';
    this.clientService.certainFacesSearch(this.photoForSearch)
      .pipe(        
        catchError(async (err) => {
          this.errorHandler(err)
          this.loadTitle = false
        }),
        tap((items) => {
          if (items?.queryNumber) {
            this.clientService.queryNumber = items.queryNumber;
          }
        }),
        tap(() => this.isGalleryLoading = false),
        tap((data) => {
          if (data) {
            data = PhotoMapper.formatImageToLocal(data)
          }
        }),
        map((data: any) => {
          if (!data) {
            return {};
          }
          this.isStatusDone = [
            ImageStatusEnum.Done,
            ImageStatusEnum.Fail,
            ImageStatusEnum.NotFound].includes(data.status);
          data.images
            .filter(item => {
              if (!this.gallerylist.images.length) {
                return true; 
              }
              return this.gallerylist.images.find(image => image.path === item.path) ? false : item;
            })
            .map((item: any) => { 
              item.thumbUri = this.sanitizer.bypassSecurityTrustUrl(item.thumbUri);
              this.gallerylist.images.push(item);
            });
          this.queryNumber = data.queryNumber;
          this.deactivateActivateButtons = data.images.length <= 10 ? 'full' : 'prev';
          return data;
        }),
        tap(() => this.loadTitle = false))
      .subscribe();
  }

  public errorHandler(err: any) {
    //throw new Error('Method not implemented.');
  }

  public onSelectItem(slide: any): any {    
    this.activePhotoCount = !slide.isChecked ? this.activePhotoCount + 1 : this.activePhotoCount - 1;
    slide.isChecked = !slide.isChecked;
    if (slide.isChecked) {
      this.gallery.push(slide);
    } else {
      this.gallery = this.gallery.filter((item: any) => item.timeStamp !== slide.timeStamp);
    }
    this.clientService.photoToCart = this.gallery;
  }

  goBack() {
    if (this.branch == 'Selfie'){
      this.router.navigate(['selfie']);
    }else
    this.router.navigate([''])
  }

  goHome() {
    this.router.navigate([''])
  }

  goSelfie() {
    this.router.navigate(['selfie'])
  }

  goNext() {
    this.router.navigate(['howPrint'])
  }

  sanatizeUrl(generatedImageUrl): SafeResourceUrl {
    return this.sanitizer.bypassSecurityTrustResourceUrl(generatedImageUrl);
  }

  /* Method to fetch image from Url */
  getBase64ImageFromURL(url: string): Observable<string> {
    return Observable.create((observer: Observer<string>) => {
      // create an image object
      let img = new Image();
      img.crossOrigin = "Anonymous";
      img.src = url;
      if (!img.complete) {
        // This will call another method that will create image from url
        img.onload = () => {
          observer.next(this.getBase64Image(img));
          observer.complete();
        };
        img.onerror = err => {
          observer.error(err);
        };
      } else {
        observer.next(this.getBase64Image(img));
        observer.complete();
      }
    });
  }

  /* Method to create base64Data Url from fetched image */
  getBase64Image(img: HTMLImageElement): string {
    // We create a HTML canvas object that will create a 2d image
    var canvas: HTMLCanvasElement = document.createElement("canvas");
    canvas.width = img.width;
    canvas.height = img.height;
    let ctx: CanvasRenderingContext2D = canvas.getContext("2d");
    // This will draw image
    ctx.drawImage(img, 0, 0);
    // Convert the drawn image to Data URL
    let dataURL: string = canvas.toDataURL("image/png");
    this.base64DefaultURL = dataURL;
    return dataURL.replace(/^data:image\/(png|jpg);base64,/, "");
  }

  getImage(imageUrl: string) {
    this.windowOPen = true;
    this.getBase64ImageFromURL(imageUrl).subscribe((base64Data: string) => {
      this.base64TrimmedURL = base64Data;
      this.createBlobImageFileAndShow();
    });
  }

  getImageWithoutWindowOpen(imageUrl: string) {
    if(this.branch == 'Neural'){
      this.seen_search_photo = true
    }else{
      this.seen_search_photo = false
    }
    this.windowOPen = false;
    this.getBase64ImageFromURL(imageUrl).subscribe((base64Data: string) => {
      this.base64TrimmedURL = base64Data;
      this.createBlobImageFileAndShow();
    });
  }

  /**Method that will create Blob and show in new window */
  createBlobImageFileAndShow(): void {
    this.dataURItoBlob(this.base64TrimmedURL).subscribe((blob: Blob) => {
      const imageBlob: Blob = blob;
      const imageName: string = this.generateName();
      const imageFile: File = new File([imageBlob], imageName, {
        type: "image/jpeg"
      });
      this.generatedImage = window.URL.createObjectURL(imageFile);
      // on demo image not open window
      if (this.windowOPen) {
        window.open(this.generatedImage);
      }
    });
  }

  /**Method to Generate a Name for the Image */
  generateName(): string {
    const date: number = new Date().valueOf();
    let text: string = "";
    const possibleText: string =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (let i = 0; i < 5; i++) {
      text += possibleText.charAt(
        Math.floor(Math.random() * possibleText.length)
      );
    }
    // Replace extension according to your media type like this
    return date + "." + text + ".jpeg";
  }

  /* Method to convert Base64Data Url as Image Blob */
  dataURItoBlob(dataURI: string): Observable<Blob> {
    return Observable.create((observer: Observer<Blob>) => {
      const byteString: string = window.atob(dataURI);
      const arrayBuffer: ArrayBuffer = new ArrayBuffer(byteString.length);
      const int8Array: Uint8Array = new Uint8Array(arrayBuffer);
      for (let i = 0; i < byteString.length; i++) {
        int8Array[i] = byteString.charCodeAt(i);
      }
      const blob = new Blob([int8Array], { type: "image/jpeg" });
      observer.next(blob);
      observer.complete();
    });
  }

}

