import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { tap } from 'rxjs/operators';
import { ActivityMode } from 'src/app/core';
import { Configuration } from 'src/app/modules/outer-config/config';
import { BrokerService } from 'src/app/shared/services/broker.service';
import { ClientService } from 'src/app/shared/services/client.service';
import { HowPrintService } from 'src/app/shared/services/howPrint.service';
import { PhotoService } from 'src/app/shared/services/photo.service';
import { SessionService } from 'src/app/shared/services/session.service';

@Component({
  selector: 'app-how-print',
  templateUrl: './how-print.component.html',
  styleUrls: ['./how-print.component.scss']
})
export class HowPrintComponent implements OnInit {
  public isDisabled:boolean = true;
  public paperPrice:number;
  public magnitePrice:number = 200;
  public emailPrice:number;
  public branch: string;

  constructor(private router: Router,
              private printService:HowPrintService,
              private brokerService:BrokerService,
              private clientService:ClientService,
              private photoService:PhotoService,
              private sessionService:SessionService) { }

  ngOnInit(): void {
    ActivityMode.startTimer();
    this.paperPrice  = this.brokerService.priceForPaper
    this.emailPrice = this.brokerService.priceForEmail
    this.branch = this.sessionService.branch;   
    switch (this.branch) {
      case 'Selfie': {        
        const images: string[] = [];      
        let toCard = this.clientService.photoToCart.map((item: any) => {
          images.push(item.path);
          return item;
        });
      this.clientService.sourceSelfi({
        images: images 
      })
      .pipe(
        tap((item: any) => {
          item.images.map((values, key)=> {
            values.originalUri = `${Configuration.host}${values.originalUri}`;
            toCard[key] = {
              ...toCard[key],
              ...values
            }
          });
          this.clientService.photoToCart = toCard;
        })
      )
      .subscribe()
        break;
      }
      case 'Neural': { 
        /*let toCard = this.clientService.photoToCart.map((item: any) => {
          item.originalUri = item.thumbUri;
          return item;
        });
        this.clientService.photoToCart = toCard;*/
      }
    }    
  }

  goBack(){
    this.router.navigate(['gallery'])
  }

  goHome(){
    this.router.navigate([''])
  }

  goCart(material:any){

    this.printService.material = material
    this.router.navigate(['cart'])
  }

  goEmail(){
    this.router.navigate(['email'])
  }

}
