import { Component, HostBinding, ViewChild, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router, RouterOutlet } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { interval, Subscription } from 'rxjs';
import { take } from 'rxjs/operators';
import { slideInAnimation } from './animation';
import { ActivityMode } from './core';
import { Configuration } from './modules/outer-config/config';
import { ClientService } from './shared/services/client.service';
import { DateService } from './shared/services/date.service';
import { NavigationService } from './shared/services/navigation.service';
import { SessionService } from './shared/services/session.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  animations: [
    slideInAnimation
    // animation triggers go here
  ]
})
export class AppComponent {
  title = 'lomobile-front';
  steps: StepEnum = StepEnum.User;
  public subscription: Subscription = new Subscription();
  public loading: any = false;
  @HostBinding('@.disabled')
  public animationsDisabled = false;
  public fotobot = false

  prepareRoute(outlet: RouterOutlet) {

    return outlet && outlet.activatedRouteData && outlet.activatedRouteData.animation;
  }


  toggleAnimations() {

    this.animationsDisabled = !this.animationsDisabled;
  }
  @ViewChild('serviceNotWorking') serviceNotWorking;
  constructor(public sessionService: SessionService,
    public router: Router,
    private modalService: NgbModal,
    public dateService: DateService,
    private clientService: ClientService,
    private navigateService: NavigationService) {
      ActivityMode.setRoute(this.router);
      this.navigateService.errorPage$.subscribe(item => {
        if (!item) {
          return;
        }
        this.modalService.open(this.serviceNotWorking, { ariaLabelledBy: 'modal-basic-title' });
        setTimeout(()=> {
          this.modalService.dismissAll();
        }, 15000);
      });
     }

  ngOnInit(): void {  
    this.clientService.getClient().subscribe((item) => {
      this.clientService.currentDevice = item;
      let typePage = item.setting.main_page;
      switch (typePage) {
        case 'neural_network_or_photo_bot':
          this.navigateService.navigate('neural')
          if (item.locationTypeId == 1) {
            this.navigateService.navigate('gallery')
            this.navigateService.photoForSearch = 'photoBot'
            this.sessionService.branch = 'Neural'
          } else {
            this.navigateService.navigate('neural')
          }
          break;

        case 'home':
          this.navigateService.navigate('main-new');
          break;
        case 'test':
          this.navigateService.navigate('test')
          break;

        case 'facebook':
          console.log('fb')
          break;

        case 'instagram':
          console.log('inst')
          break;

        case 'vk':
          console.log('vk')
          break;

        default:
          this.navigateService.navigate('main')
          break;
      }

    })
    this.sessionService.activity$
      .subscribe((item: any) => {
        switch (this.steps) {
          case StepEnum.Guest: {
            this.loading = true;
            setTimeout(() => {
              this.loading = false;
              this.steps = item ? StepEnum.User : StepEnum.Guest;
            }, 1000);
            break;
          }
          case StepEnum.Exit: {
            this.steps = StepEnum.Guest;
            break;
          }
        }
        this.subscription.unsubscribe();

        if (item) {
          this.subscription = interval(Number(Configuration.session.expiryDate)).pipe(take(1)).subscribe(val => {
            this.steps = StepEnum.Guest;
            this.sessionService.session = null;
            // location.reload();
          });
        }
      });
  }

  public ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  public cancel($event: any) {
    this.sessionService.session = null;
    this.steps = StepEnum.Exit;
  }
}

export enum StepEnum {
  User = 'User',
  Guest = 'Guest',
  Exit = 'Exit'
}


