import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { interval, Subject, timer,  } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ActivityMode } from 'src/app/core';
import { ClientService } from 'src/app/shared/services/client.service';
import { NavigationService } from 'src/app/shared/services/navigation.service';

@Component({
  selector: 'app-print-done',
  templateUrl: './print-done.component.html',
  styleUrls: ['./print-done.component.scss']
})
export class PrintDoneComponent implements OnInit {
  printWorking: boolean = true;
  printWorkingTimer: number = 15;
  completePrintWorking$: Subject<any> = new Subject();
  public message: string = '';

  constructor(
    private router: Router,
    private clientService: ClientService,
    private navigateService: NavigationService,
  ) { }

  ngOnInit(): void {  
    ActivityMode.stopTimer();      
    this.navigateService.clearURL();

    if (this.clientService.email) {
      this.message = 'Ваши фото отправлены';
      this.printWorking = false;
      return;
    }
    const mainInterval = setInterval(() => {
      this.printWorkingTimer--;
      if (this.printWorkingTimer <=0)  {
        this.printWorking = false;
        this.message = 'Фотографии напечатаны!';
        clearInterval(mainInterval);
        ActivityMode.routePath = '';
        ActivityMode.startTimer(15000);
      };
    }, 1000);
  }
}
