import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import { NeuralComponent } from './components/neural/neural.component';
import { MainPageComponent } from './components/main-page/main-page.component';
import { MainPageComponent as MainNewComponent } from './components/v1/main-page/main-page.component';
import { GalleryComponent } from './components/gallery/gallery.component';
import { HowPrintComponent } from './components/how-print/how-print.component';

// Import library module
import { PreLoadComponent } from './components/pre-load/pre-load.component';
import { PhotoNotFoundComponent } from './components/photo-not-found/photo-not-found.component';
import { PaymentFormatComponent } from './components/payment-format/payment-format.component';
import { PrintComponent } from './components/print/print.component';
import { PrintDoneComponent } from './components/print-done/print-done.component';
import { ErrorPopupComponent } from './components/error-popup/error-popup.component';
import { ReceiptPopupComponent } from './components/receipt-popup/receipt-popup.component';
import { CartComponent } from './components/cart/cart.component';
import { PayComponent } from './components/pay/pay.component';
import { SendEmailComponent } from './components/send-email/send-email.component';
import { DatepickerComponent } from './shared/datepicker/datepicker/datepicker.component';
import { LocationComponent } from './shared/locations/location/location.component';
import { SelfieComponent } from './components/selfie/selfie.component';
import { SmartphoneComponent } from './modules/download-module/smartphone/smartphone/smartphone.component';
import { HeaderComponent } from './shared/header/header.component';
import { DownloadComponent } from './modules/download-module/download/download/download.component';
import { SocialComponent } from './modules/download-module/social/social/social.component';
import {WirelessComponent} from "./modules/wireless-download/components/wireless/wireless.component";
import { AppComponent } from './app.component';

const routes: Routes =[
  {path:'',component: AppComponent},
  {path:'main',component: MainPageComponent},
  {path:'main-new',component: MainNewComponent},
  { path: 'selfie', component: SelfieComponent},
  { path: 'neural', component: NeuralComponent, data: {animation: 'SelfiPage'}},
  { path: 'gallery', component: GalleryComponent,data: {animation: 'GalleryPage'}},
  { path: 'howPrint', component: HowPrintComponent,data: {animation: 'HowPrintPage'}},
  { path: 'download', component: DownloadComponent},
  { path: 'wi-fi', component: WirelessComponent, outlet: 'wifi'},
  { path: 'smartphone', component:SmartphoneComponent},
  { path: 'header', component:HeaderComponent},
  { path: 'social', component:SocialComponent},

  { path: 'cart', component: CartComponent,data: {animation: 'Cart'}},
  { path: 'email', component: SendEmailComponent},

  { path: 'paymentFormat', component: PaymentFormatComponent,data: {animation: 'PaymentFormat'}},
  { path: 'pay', component: PayComponent,data: {animation: 'Pay'}},
  { path: 'check', component: ReceiptPopupComponent,data: {animation: 'Check'}},
  { path: 'print', component: PrintComponent,data: {animation: 'Print'}},
  { path: 'print-done', component: PrintDoneComponent},  
  { path: 'error', component: ErrorPopupComponent},

  { path: 'preloader', component: PreLoadComponent},
  { path: 'not', component: PhotoNotFoundComponent},
  { path: 'datepicker', component: DatepickerComponent},
  {path: 'test', loadChildren: () => import('./test/test.module').then(m => m.TestModule)},
  { path: 'location', component: LocationComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
