import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable, Subject} from 'rxjs';
import { Configuration } from 'src/app/modules/outer-config/config';
import { GalleryInterface } from '../interfaces/gallery.interface';
import { ClientInterface } from '../interfaces/client.interface';
import { PhotoSearchInterface } from '../interfaces/photoSearch.interface';
import { IBasket } from '../interfaces/basket.interface';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { ImageInterface } from '../interfaces/image.interface';
import { PhotoService } from './photo.service';
import { LocationInterface } from 'src/app/core/interfaces/location.interface';
import { tap } from 'rxjs/operators';



@Injectable({
  providedIn: 'root'
})
export class ClientService {
  public location:string; //Deprecated
  public locationID:string; //Deprecated
  public Location: LocationInterface;
  public date:string;
  public photoToCart: ImageInterface[] = [];
  public photoToPaper: ImageInterface[] = [];
  public queryNumber:number;
  public email:string;
  public photoCountInOrder:number
  public ordID:number
  public currentDevice: ClientInterface;
  public neiron: Subject<any> = new Subject<any>();

  constructor(private http: HttpClient,
              private photoService:PhotoService) {
  }

  public getClient(): Observable<ClientInterface> {
    return this.http.get<ClientInterface>(`${Configuration.host}api/client`); 
  }

  public getListImage(): Observable<GalleryInterface> {
    return this.http.get<GalleryInterface>(`${Configuration.host}api/client/source/image-directory-faces`);
  }

  public createOrder(basket: IBasket): Observable<any> {
    return this.http.post(`${Configuration.host}api/client/order`, JSON.stringify(basket), {
      headers: {
        'Content-Type': 'application/json'
      },
      observe: "response"
    });
  }

  public getCheck(orderId: number):Observable<any>{
    return this.http.get(`${Configuration.host}api/client/order/${orderId}/invoice`)
  }

  public certainFacesSearch(photoBase64: PhotoSearchInterface): Observable<any> { 
    return this.http.post(`${Configuration.host}api/client/source/certain-faces-search`, JSON.stringify(photoBase64), {
      headers: {
        'Content-Type': 'application/json',
          }
    });
  }

  public sourceSelfi(photoBase64mas: any): Observable<any> {
    return this.http.post(`${Configuration.host}api/client/source/selfie`,(photoBase64mas), {
      headers: {
        'Content-Type': 'application/json',
          }
    });
  }

  public getDates(location: string): Observable<any> {
    return this.http.post(`${Configuration.host}api/client/source/get-dates`, {
      location
    }, {
      headers: {
        'Content-Type': 'application/json'
      }
    });
  }

  public completeOrder(orderId: number): Observable<any> {
    return this.http.get<any>(`${Configuration.host}api/client/order/${orderId}/execute`, {
      headers: {
        'Content-Type': 'application/json'
      }
    });
  }

  public standBy(): Observable<any> {
    return this.http.post(`${Configuration.host}api/client/state/stand-by`, {}, {
      headers: {
        'Content-Type': 'application/json'
      },
      observe: "response"
    });
  }

}
