import { NgbDateStruct, NgbDate } from '@ng-bootstrap/ng-bootstrap';
import * as moment from 'moment';

export class DateMapper {
    public static formatNgbDateStruct(date: any): NgbDateStruct {
        const momentDate: moment.Moment = moment(date);
        return {
            year: Number(momentDate.format('YYYY')),
            month: Number(momentDate.format('MM')),
            day: Number(momentDate.format('DD')) 
        }
      }
    
      public static getOutDates(array: string[], from: string, to: string) {
        let outDates = [];
        let isWork = true;
        const momentFrom = moment(from);
        let start = new NgbDate( 
            Number(momentFrom.format('YYYY')), 
            Number(momentFrom.format('MM')), 
            Number(momentFrom.format('DD')));
        let toMoment = moment(to);
        let end = new NgbDate(
                Number(toMoment.format('YYYY')), 
                Number(toMoment.format('MM')), 
                Number(toMoment.format('DD')));
        let currentDataM = momentFrom;
        let counter = 40; 
        while (isWork ) {
            counter--;
            currentDataM = moment(currentDataM).add(1, 'days');
            let currentDate = new NgbDate(
                Number(currentDataM.format('YYYY')), 
                Number(currentDataM.format('MM')), 
                Number(currentDataM.format('DD'))
            );
            if (currentDate.equals(end) || !counter) { 
                isWork = false;
                continue; 
            }
            if (array.includes(currentDataM.format('YYYY-MM-DD'))) {
                continue;
            }
            outDates.push(currentDate);
        }        
        return outDates;
      }
}