import { Component, OnInit } from '@angular/core';
import { NavigationService } from 'src/app/shared/services/navigation.service';
import { SmartphoneService } from '../smartphone.service';
import {ClientService} from "../../../../shared/services/client.service";

@Component({
  selector: 'app-smartphone',
  templateUrl: './smartphone.component.html',
  styleUrls: ['./smartphone.component.scss']
})
export class SmartphoneComponent implements OnInit {
  public resourse: ResourseEnum;
  public header: boolean = true;
  public title: string;
  public websiteForDownload: string = '';

  constructor(private smartphoneService: SmartphoneService,
              private clientService: ClientService,
              private navigate:NavigationService) { }

  ngOnInit(): void {
    switch (this.smartphoneService.uploadMethod) {
      case 'wifi':
        this.resourse = ResourseEnum.WiFi
        break;
      case 'site':
        this.resourse = ResourseEnum.Site
        this.websiteForDownload = this.clientService.currentDevice.downloadViaTheSite
          .replace('http://', '')
          .replace('https://', '')
        break;
      case 'email':
        this.resourse = ResourseEnum.Email
        break;
      case 'telegram':
        this.resourse = ResourseEnum.Telegram
        break;
    }

    this.navigate.clearURL();
  }

}

export enum ResourseEnum {
  WiFi = 'wifi',
  Site = 'site',
  Email = 'email',
  Telegram = 'telegram',
}
