import { Router } from "@angular/router";
import * as moment from "moment";
import { Configuration } from "src/app/modules/outer-config/config";

export class ActivityMode {
    public static routePath: string = '';
    public static timer: number;
    public static runned: any[];
    public static statusTimer = false;
    public static statusTimerRunned: number;
    public static timeOut: any;
    public static route: Router;
    public static setRoute = (route) => ActivityMode.route = route;
    public static setRoutePath = (routePath) => ActivityMode.routePath = routePath || '';
    public static startTimer = (timer?) => {
        ActivityMode.timer = timer || Configuration.activityTimer * 1000;
        //console.log('startTimer with time:', ActivityMode.timer);
        ActivityMode.runned = ActivityMode.runned || [];
        let time = setTimeout(() => {                        
            ActivityMode.runned.pop();
            //console.log('All runned timer', ActivityMode.runned);
            if (ActivityMode.runned.length) {
                return;
            }          
            ActivityMode.route.navigate([ActivityMode.routePath]);
            ActivityMode.routePath = '';         
        }, ActivityMode.timer);
        ActivityMode.runned.push(time);
    }
    public static stopTimer = () => {
        //console.log('stopTimer');
        if (ActivityMode.runned) {
            ActivityMode.runned.pop();
        }        
        clearTimeout(ActivityMode.timeOut);
    }
    /* Перезагрузити таймер якщо він запущений */
    public static resetTimer = () => {
        //console.log('resetTimer');
        ActivityMode.startTimer();
    }
}