import { AfterContentChecked, AfterViewInit, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChange, ViewEncapsulation } from '@angular/core';
import Keyboard from 'simple-keyboard';

@Component({
  selector: 'app-virtual-keyboard',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './virtual-keyboard.component.html',
  styleUrls: ['./virtual-keyboard.component.scss',
  '../../../../../node_modules/simple-keyboard/build/css/index.css',
]
})
export class VirtualKeyboardComponent implements OnInit, AfterViewInit, AfterContentChecked {
  @Input()
  public value: any;
  public keyboard: Keyboard;

  @Output()
  changeCurrentValue: EventEmitter<string> = new EventEmitter<string>();
  @Output()
  closeKeyboard: EventEmitter<boolean> = new EventEmitter<boolean>();

  

  constructor() { }

  ngOnInit(): void {
  }

  ngAfterContentChecked() {
  }

  
  ngAfterViewInit() {
    this.keyboard = new Keyboard({
      //onChange: (input) => this.onChangeKeyboard.call(this, input),
      onKeyPress: (button) => this.onKeyPress.call(this, button),
      theme: "hg-theme-default myTheme1",
      layout: {
        default: [
          "1 2 3 4 5 6 7 8 9 0",
          "q w e r t y u i o p",
          "a s d f g h j k l",
          "z x c v b n m",
          "{bksp} . _ - @ {confirm}"
        ],
        // shift: [
        //   "Q W E R T Y U I O P {bksp}",
        //   "A S D F G H J K L {enter}",
        //   "{shiftactivated} Z X C V B N M {shiftactivated}",
        //   "{alt} {space} , . @ _ -"
        // ],
        
      },
      display: {
        "{confirm}":"Готово",
        "{bksp}": "⌫",
      },
      buttonTheme: [
        {
          class: 'confirm',
          buttons: 'Готово',
        }
      ]
    });
  }

  /*onChangeKeyboard = (input: string) => {
    this.value += input;
    this.changeCurrentValue.emit(this.value);
  };*/

  onKeyPress = (button: string) => {
    console.log('onKeyPress', button);
    // if (button === "{default}" ||
    //     button === "{alt}" ||
    //     button === "{shiftactivated}" ||
    //     button === "{shift}" ) this.handleLayoutChange(button);
    switch (button) {
      case '{bksp}': {
        this.value = this.value.substring(0, this.value.length-1);
        break;
      }
      case '{confirm}': {
        this.closeKeyboard.emit(true);
        return;
      }
      default: {
        this.value += button;        
        break;
      }
    }
    this.changeCurrentValue.emit(this.value);
    return;
  };

  onBlurField(value: any) {
    console.log('onBlurField model', value);
    this.keyboard.setInput(this.value);
  }

  handleLayoutChange(button: any) {
    let currentLayout = this.keyboard.options.layoutName;
    let layoutName;

    switch (button) {
      // case "{shift}":
      // case "{shiftactivated}":
      // case "{default}":
      //   layoutName = currentLayout === "default" ? "shift" : "default";
      //   break;

      // case "{alt}":
      // case "{altright}":
      //   layoutName = currentLayout === "alt" ? "default" : "alt";
      //   break;

      // default:
      //   break;
    }

    if (layoutName) {
      this.keyboard.setOptions({
        layoutName: layoutName
      });
    }
  }

}
