import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { NgbDateStruct, NgbCalendar, NgbDatepicker, NgbDatepickerI18n, NgbDate } from '@ng-bootstrap/ng-bootstrap';
import * as moment from 'moment';
import { Observable } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { DateMapper } from 'src/app/core/mappers/date.mapper';
import { ClientService } from '../../services/client.service';
import { NavigationService } from '../../services/navigation.service';
@Component({
  selector: 'app-datepicker',
  templateUrl: './datepicker.component.html',
  styleUrls: ['./datepicker.component.scss'],
  encapsulation: ViewEncapsulation.None,
  styles: [`
    .custom-datepicker .ngb-dp-header {
      padding: 0;
    }
    .custom-datepicker .ngb-dp-content {
      display: grid;
      grid-template-columns: auto auto;
      grid-column-gap: 1rem;
      grid-row-gap: 0.5rem;
    }
  `]
})

export class DatepickerComponent implements OnInit {
  public model: NgbDateStruct;
  public dataSlotList$: Observable<string[]>;
  public date: { year: number, month: number };
  public isLoading: boolean;
  public minDate: NgbDateStruct;
  public maxDate: NgbDateStruct;  
  @ViewChild(NgbDatepicker, {static: true}) datepicker: NgbDatepicker;
  disabledDates: NgbDateStruct[] = [];
  isDisabled = (d:NgbDateStruct, current: {month: number,year:number}) => 
   this.disabledDates.find(x=>new NgbDate(x.year,x.month,x.day).equals(d))? true : false;

  constructor(private router: Router,
    private calendar: NgbCalendar,
    private clientService: ClientService,
    public i18n: NgbDatepickerI18n,
    private navigateService:NavigationService) {
  }

  ngOnInit(): void { 
    if (!this.clientService.Location) {
      return;
    }
    this.getData();
  }

  private getData() {
    this.isLoading = true;
    this.dataSlotList$ = this.clientService.getDates(this.clientService.Location?.id)
      .pipe(
        map((list: any[]) => {       
          if (!list.length) {             
            return [];
          }
          this.disabledDates = DateMapper.getOutDates(list, list[0], list[list.length -1]);
          this.minDate = DateMapper.formatNgbDateStruct(list[0]); 
          this.maxDate = DateMapper.formatNgbDateStruct(list[list.length -1]); 
          this.navigateService.clearURL();
          return list;
        })
      );
  }
   
  navigate(number: number) {
    if (!this.datepicker) {
      return;
    }
    const {state, calendar} = this.datepicker;
    this.datepicker.navigateTo(calendar.getNext(state.firstDate, 'm', number));
  }

  public formatDate(number:any):string {
    return number < 10 ? `0${number}` : number
  }

  public selectDay(date: any):any {
    const stringifyData = `${this.formatDate(date.day)}.${this.formatDate(date.month)}.${date.year}`
    const stringifyMinData = `${this.formatDate(this.minDate.day)}.${this.formatDate(this.minDate.month)}.${this.minDate.year}`
    const stringifyMaxData = `${this.formatDate(this.maxDate.day)}.${this.formatDate(this.maxDate.month)}.${this.maxDate.year}`    
    if((date >= this.minDate && date <= this.maxDate)&&(date.day != undefined))
    {
      this.clientService.date = stringifyData;
      this.navigateTo('neural');
    }
  }

  public navigateTo(rout:string): any {
    this.navigateService.navigate(rout);
  }

}
