import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ActivityMode } from 'src/app/core';
import { HowPrintService } from 'src/app/shared/services/howPrint.service';
import { PayMethodEnum } from '../pay/pay.component';

@Component({
  selector: 'app-payment-format',
  templateUrl: './payment-format.component.html',
  styleUrls: ['./payment-format.component.scss']
})
export class PaymentFormatComponent implements OnInit {
  public disabled:boolean = true;

  constructor(private router: Router,
              private printService:HowPrintService) { }

  ngOnInit(): void {
    ActivityMode.startTimer();
  }

  goBack(){
    this.router.navigate(['cart'])
  }

  goHome(){
    this.router.navigate([''])
  }

  goNext(method:any){
    this.printService.method = method;
    this.router.navigate(['pay'])
  }

}
