import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, of, Subject, Subscription, throwError } from 'rxjs';
import { catchError, delay, take, takeUntil, tap } from 'rxjs/operators';
import { Configuration } from 'src/app/modules/outer-config/config';
import { IBasket } from 'src/app/shared/interfaces/basket.interface';
import { SocketInterface } from 'src/app/shared/interfaces/socket.interface';
import { BrokerService } from 'src/app/shared/services/broker.service';
import { ClientService } from 'src/app/shared/services/client.service';
import { webSocket, WebSocketSubject } from "rxjs/webSocket"
import { HowPrintService } from 'src/app/shared/services/howPrint.service';
import { StepEnum } from 'src/app/app.component';
import { SessionService } from 'src/app/shared/services/session.service';
import { ProductInterface } from 'src/app/shared/interfaces/product.interface';
import { ImageInterface } from 'src/app/shared/interfaces/image.interface';
import { IOrderItem } from 'src/app/shared/interfaces/order.interface';
import { PhotoService } from 'src/app/shared/services/photo.service';
import { NavigationService } from 'src/app/shared/services/navigation.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { environment } from 'src/environments/environment';
import { DateService } from 'src/app/shared/services/date.service';
import { ActivityMode } from 'src/app/core';

@Component({
  selector: 'app-pay',
  templateUrl: './pay.component.html',
  styleUrls: ['./pay.component.scss']
})
export class PayComponent implements OnInit {
  public method: PayMethodEnum;
  public error: boolean = false;
  public messages: any[] = [];
  public viewPay: any;
  public viewPage: any;
  public photoFromCart: ImageInterface[] = [];
  public completeOderSub: Subscription;
  public createOrderSub: Subscription;

  public basket: IBasket = {
    queryNumber: this.clientService.queryNumber,
    terminal: this.clientService.locationID,
    client_email: this.clientService.email,
    start_at: Math.floor(Date.now() / 1000),
    items: []
  }

  @Output()
  public changeQR: EventEmitter<string> = new EventEmitter<string>()

  //private sub$: Subject<boolean> = new Subject();
  private subject: WebSocketSubject<SocketInterface> = webSocket(Configuration.tradeService);
  public fiatSubject: WebSocketSubject<SocketInterface> = webSocket(Configuration.fiatService);
  public images$: Observable<ImageInterface[]> = of([]);
  public allSum: number;
  public timeLeft: number = 40
  public interval;
  steps: StepEnum = StepEnum.Guest;
  public subscription: Subscription = new Subscription();
  @Input()
  public products: ProductInterface[];


  public received: number = 0;
  public remein: number = 0;
  @ViewChild('serviceCheckIsNotWork') serviceCheckIsNotWork;
  @ViewChild('errorState') errorState;
  
  constructor(private router: Router,
    private clientService: ClientService,
    public modalService: NgbModal,
    private brokerService: BrokerService,    
    private navigateService: NavigationService,
    public printService: HowPrintService,
    public dateService: DateService,
    private sessionService: SessionService,
    private photoService: PhotoService) {       
    }

  triggerModal(content:any) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'});    
  }

  ngOnInit(): void {
    ActivityMode.stopTimer();      
    this.navigateService.clearURL();         
    this.messages = [];
    this.allSum = this.brokerService.sumForPay;        
    this.method = this.printService.method;        
    this.photoService.photo$
    .pipe(
      take(1)
    )
    .subscribe((items: any[]) => {      
      items.map((item) => {
        const current: IOrderItem = {
          product_id: 0,
          product_count: 0,
          brand_id: 0,
          image_path: '',
          remote_path: ''
        }
        if (item.sumOfPaper) {         
          current.product_id = 1;
          current.product_count = item.totalCount;
          current.brand_id = item.brandId;
          current.image_path = item.path
          current.remote_path = item.originalUri;
          this.basket.items?.push(current)
        }
        if (item.sumOfEmail) {          
          current.product_id = 101;
          current.product_count = item.totalCount;
          current.brand_id = item.brandId;
          current.image_path = item.path
          current.remote_path = item.originalUri;
          this.basket.items?.push(current)
        }
      });      
      switch (this.method) {
        case 'Card':
          this.getProofOfPayment();
          break;
        case 'Union':
          this.remein = this.allSum;
          this.fiatPay(); 
          break;
      }      
    })    
  }

  public startTimer() {
    this.interval = setInterval(() => {
      if (this.timeLeft > 0) {
        this.timeLeft--;
      } else {
        this.steps = StepEnum.Guest;
        this.sessionService.session = null;
        this.router.navigate([''])
      }
    }, 1000)
  }
  //card
  private getProofOfPayment() {
    this.createOrderSub = this.clientService.createOrder(this.basket)
      .pipe(
        catchError((err) => throwError(err))
      )
      .subscribe((response) => {
        if (response.status === 200) {    
          this.clientService.ordID = response.order_id
          this.sendMessage(this.subject, {
            "command": "sale",
            "amount": response.body.amount_penny
          })            
          this.subscribeCardPay(response);
          return;
        };

        this.error = true;          
      })
  }

  private completeOrder(orderId: any) {
    this.completeOderSub = this.clientService.completeOrder(orderId)
    .pipe(
      catchError((err) => {
        this.viewPage = 'error';
        return throwError(err);
      }),
      tap(() => this.viewPay = true)
    )
    .subscribe(complete => {
      if (complete) {
        this.basket = {
          queryNumber: 1,
          client_email: this.clientService.email,
          start_at: Math.floor(Date.now() / 1000),
          terminal: this.clientService.locationID,
          items: []
        }
        this.changeQR.emit(complete.qr);
        console.log('completeOrder', complete);
        if (environment.printCheck) {
          this.router.navigate(['check'])
          return;
        }
        this.showMessageCheckIsNotWork('print-done');    
      }
    });
  }

  //gotivka
  private fiatPay() {
    this.createOrderSub = this.clientService.createOrder(this.basket)
      .pipe(
        catchError((err) => throwError(err))
      )
      .subscribe((response) => {
        if (response.status !== 200) {
          this.error = true;
          return;
        }
        console.log("Сумма до приема денег" + response.body.amount_penny)
        this.clientService.ordID = response.body.order_id
        this.sendMessage(this.fiatSubject, {
          "object": "cashmachine",
          "cmd": "enable",
          "enable": true,
        });
        this.subscribeFiatPay(response);
      })
  }

  private sendMessage(service: any, message) {
    this.messages.push(message);
    service.subscribe()
    service.next(message);
    //service.unsubscribe();
  }

  private subscribeFiatPay(response) {
    this.fiatSubject.pipe(
      catchError((err) => {
        console.log("Connect error");
        if (environment.production) {
          this.showCassaIsNotWork('gallery');
          return throwError(err);
        } 
        this.showMessageCheckIsNotWork();
        return throwError(err);
      }),
    )
    .subscribe((message: any) => {
      let value = message.event || message.result;
      console.log('From ws message', message);
      if (this.messages.length) {
        const lastValue = this.messages[this.messages.length -1];
        if (lastValue.enable == false && message.result == 'ok') {
          value = "sucessfull";
        }
      }
      switch (value) {
        case 'ok': { 
          if (environment.endingPayment) {
            //ТЕСТ Завершение приема денег
            this.sendMessage(this.fiatSubject, {
              "object": "cashmachine", 
              "cmd": "enable", 
              "enable": false
             });
          }
           break;
        }
        case 'banknote': {
          this.received += (message.eventValue / 100)
          this.remein = ((this.remein * 100) - message.eventValue) / 100
          if (this.remein < 0) this.remein = 0;
          if (this.remein <= 0) {
            // Завершение приема денег
            this.sendMessage(this.fiatSubject, {
              "object": "cashmachine", 
              "cmd": "enable", 
              "enable": false
             });
          }
          break;
        }
        case 'rejecting': {
          console.log("Банкнота не принята")
          this.error = true;
          break
        }
        case 'coin': {
          console.log("Принята монета")
          break;
        }
        case 'sucessfull': {
          console.log('Прием денег закончен');
          this.brokerService.acquiring({
            "command": "sale",
            "status": "success",
            "amount": response.body.amount_penny,
          }).pipe(
            catchError((err) => {
              this.error = true
              return throwError(err);
            })
          ).subscribe(balance => {
            if (balance.status === 200) {
              this.viewPay = false;
              this.completeOrder(this.clientService.ordID);                
            }
          })
          break;
        }
      }      
    })
  }
  private subscribeCardPay(response) {
    this.subject.pipe(
      catchError((err) => {
        console.log("Connect error");
        if (environment.production) {
          this.showCassaIsNotWork('gallery');
          return throwError(err);
        } 
        this.showMessageCheckIsNotWork();
        return throwError(err);
      }),
      tap((item) => console.log(item))
    )
    .subscribe((message: any) => {
      console.log('message from ws:', message);
      switch (message.status) {                    
        case 'success': {
          this.clientService.ordID = response.body.order_id
          this.brokerService.acquiring(message).pipe(
            catchError((err) => {
              console.log(16)
              this.error = true
              return throwError(err);
            })
          ).subscribe(balance => {
            if (balance.status === 200) {
              this.viewPay = false;
              console.log("balance status 200")
              this.completeOrder(this.clientService.ordID);
            }
          })
          break;
        }
        case 'error': {
          this.brokerService.acquiring(message).pipe(
            catchError((err) => {
              console.log(18)
              this.error = true
              return throwError(err);
            })
          ).subscribe(balance => {
            if (balance.status === 200) {
              this.viewPage = "error"
              this.error = true
            }
          })
          break;
        }
      }
    },
    )
  }

  private showCassaIsNotWork(route?: string) {
    this.error = true;
    setTimeout(()=> {
      this.error = false;
      this.modalService.dismissAll();                  
      route && this.router.navigate([route]);
    }, 4000);
  }

  private showMessageCheckIsNotWork(route?: string) {
    this.triggerModal(this.serviceCheckIsNotWork);
    setTimeout(() => {
      this.error = false;
      this.modalService.dismissAll();      
      route && this.router.navigate([route]);
    }, 4000);
  }
  
  goBack(){
    this.router.navigate(['cart'])
  }
  goGallery() {
    this.router.navigate(['gallery']);
  }
  goNext() {
    this.router.navigate(['check'])
  }
  goHome(){
    this.router.navigate([''])
  }

  onRedirectFromErrorPopUp(router: string) {
    console.log('onRedirectFromErrorPopUp', router);
    this.navigateService.navigate(router);
  }

  ngOnDestroy(): void {
    this.timeLeft = 2;
    this.completeOderSub && this.completeOderSub.unsubscribe();
    this.createOrderSub && this.createOrderSub.unsubscribe();
    this.fiatSubject.unsubscribe();
    this.subject.unsubscribe();   
  }
}

export enum PayMethodEnum {
  Card = 'Card',
  Union = 'Union',
  QR = 'QR'
}

export enum PayEnum {
  Magnit = 'Magnit',
  Paper = 'Paper'
}