import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import {ModalDismissReasons, NgbModal} from '@ng-bootstrap/ng-bootstrap';
import { ActivityMode } from 'src/app/core';
import { NavigationService } from 'src/app/shared/services/navigation.service';

@Component({
  selector: 'app-error-popup',
  templateUrl: './error-popup.component.html',
  styleUrls: ['./error-popup.component.scss']
})
export class ErrorPopupComponent implements OnInit {
  @Input() isChild: boolean;
  closeModal: any;
  @ViewChild('modalData') content;
  @Output('redirect') redirect: EventEmitter<any> = new EventEmitter();
  
  constructor(
    private modalService: NgbModal,
    private navigateService: NavigationService,
    private router:Router) {}
    
  triggerModal(content:any) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((res) => {
      this.closeModal = `Closed with: ${res}`;
    }, (res) => {
      this.closeModal = `Dismissed ${this.getDismissReason(res)}`;
    });
  }
    
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }
  
  ngAfterViewInit():void{
    this.triggerModal(this.content);
    if (!this.isChild) {
      ActivityMode.startTimer();
    }
  }

  ngOnInit(): void {
    this.navigateService.clearURL();
  }

  onRouter(router: string) {
    if (this.isChild) {
      this.redirect.emit(router);
      return;
    }
    this.router.navigate([router]);
  }
}
