import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HttpResponse } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable, of } from "rxjs";
import { map } from "rxjs/operators";
import { Configuration } from "../modules/outer-config/config";

@Injectable()
export class ExpiryInterceptor implements HttpInterceptor {
    constructor() { }

    public intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        if(request.url.includes((`${Configuration.host}api/client`))){
            return next.handle(request).pipe(map((event: HttpEvent<any>) => {
                if (event instanceof HttpResponse) {
                    event = event.clone({body: this.modifyBody(event.body)});
                }
    
                return event;
            }));
        }else 
        return next.handle(request);
        
    }

    private modifyBody(body: any) {
        
        // if(body.setting?.time_to_advertise){
        //     let time = body.setting.time_to_advertise + "000";
        //     Configuration.session.expiryDate = time;
        //     console.log("adv " + time)
        // }
    }

}


