import { Component, EventEmitter, OnInit, Output, Renderer2, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { WebcamImage } from 'ngx-webcam';
import { interval, Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { BrokerService } from 'src/app/shared/services/broker.service';
import { CameraService } from 'src/app/shared/services/camera.service';
import { ClientService } from 'src/app/shared/services/client.service';
import { MocksService } from 'src/app/shared/services/mocks.service';
import { NavigationService } from 'src/app/shared/services/navigation.service';
import { PhotoService } from 'src/app/shared/services/photo.service';
import { trigger, style, animate, transition } from '@angular/animations';
import { Configuration } from 'src/app/modules/outer-config/config';
import { ActivityMode } from 'src/app/core';
import { SessionService } from 'src/app/shared/services/session.service';

export const fadeInOut = (name = 'fadeInOut', duration = 0.1) =>
  trigger(name, [
    transition(':enter', [
      style({ opacity: 0 }),
      animate(`${duration}s ease-in-out`)
    ]),
    transition(':leave', [animate(`${duration}s ease-in-out`, style({ opacity: 0 }))])
  ])

@Component({
  selector: 'app-selfie',
  templateUrl: './selfie.component.html',
  styleUrls: ['./selfie.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class SelfieComponent implements OnInit {
  @Output()
  public photoForSearch: EventEmitter<any> = new EventEmitter<any>();
  public selfie:string[] = []
  public webcamImage: WebcamImage | null = null;
  public takePhoto: EventEmitter<boolean> = new EventEmitter<boolean>();
  public imageClick: EventEmitter<void>
  public timeLeft: number;
  public timeFlash: number;
  public blur: boolean;
  public flash: boolean;
  public load:boolean;
  private flashElement: any;
  
  handleImage(webcamImage: WebcamImage) {
    this.webcamImage = webcamImage;
  }  

  constructor(private navigate:NavigationService,
    private router: Router,
    private clientService: ClientService,
    private cameraService: CameraService,
    private renderer2: Renderer2,
    private sessionService: SessionService,
    private brokerService: BrokerService,
    private photoService:PhotoService) { 
      this.timeLeft = Configuration.pauseCamera;
    }

  ngOnInit(): void {
    ActivityMode.startTimer();    
    this.webcamImage = null;
    this.navigate.clearURL();
    this.clientService.getClient().subscribe((item) => {
      this.cameraService.setRotateCamera(item.devices.webCamera.rotate);
      this.brokerService.priceForPaper = (item.products[0].costPenny / 100)
      this.brokerService.priceForEmail = (item.products[1].costPenny / 100)
    })
    this.clientService.standBy().subscribe((item) => {
      this.cameraService.setRotateCamera(item.body.devices.webCamera.rotate);
    })    
    this.photoService.selfiBase64 = [];
    this.photoService.selfieMas = [];
    this.preparationForPhoto();    
  }

  public getPhotoForSearch(event: any) {
    this.photoForSearch = event;
  }
  private isStopTimer$: any = new Subject();
  private isStopCameraTimer$: any = new Subject();
  
  private fullScreenMode(status: boolean) {
    const element = document.getElementsByTagName('body')[0];
    if (status) {
      this.renderer2.addClass(element, 'overflow-hidden');
      return;
    }
    this.renderer2.removeClass(element, 'overflow-hidden');
  }
  private runFlashMoment() {
    this.timeFlash--;      
    this.renderer2.addClass(this.flashElement, 'flash');           
    this.flash = !this.flash;
    this.takePhoto.emit(true);
    this.photoForSearch.emit({
      sampleImage: this.webcamImage?.imageAsDataUrl!
    });
    this.photoService.selfiBase64.push(this.webcamImage.imageAsDataUrl)            
    let img = this.webcamImage.imageAsDataUrl.replace('data:image/jpeg;base64,','');
    this.photoService.selfieMas.push(img);
    this.blur = true;
    setTimeout(() => this.renderer2.removeClass(this.flashElement, 'flash'), 100);
    if (!this.timeFlash) {
      this.isStopCameraTimer$.next(true);
      this.flash = false;
      this.fullScreenMode(false);
      this.router.navigate(['gallery']);
    }                 
  }
  
  private startMakePhotoCamera() {
    this.timeFlash = this.cameraService.timeFlash;
    this.flashElement = document.querySelector('.flash-container');    
    this.runFlashMoment();
    interval(1000)
    .pipe(takeUntil(this.isStopCameraTimer$))
    .subscribe(this.runFlashMoment.bind(this));
  }

  public preparationForPhoto() {
    this.fullScreenMode(true);
    interval(1000)
    .pipe(
      takeUntil(this.isStopTimer$)
    )
    .subscribe(() => {
      this.timeLeft--;
      if (this.timeLeft == 0) {
        this.isStopTimer$.next(true);
        this.startMakePhotoCamera();
        return;
      }           
    })
  }

  public navigateTo(rout:string):void{
    this.blur = true;
    this.takePhoto.emit(true);
    this.preparationForPhoto();
  }

}
