import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, NgModel, Validators } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { ActivityMode } from 'src/app/core';
import { ImageInterface } from 'src/app/shared/interfaces/image.interface';
import { BrokerService } from 'src/app/shared/services/broker.service';
import { ClientService } from 'src/app/shared/services/client.service';
import { PhotoService } from 'src/app/shared/services/photo.service';

@Component({
  selector: 'app-send-email',
  templateUrl: './send-email.component.html',
  styleUrls: ['./send-email.component.scss']
})
export class SendEmailComponent implements OnInit {
  public slides:ImageInterface[] = [];
  public sumCount: number = 0;
  
  slideConfig = {
    autoplay: false,
    vertical: false,
    verticalSwiping: false,
    slidesToShow: 3,
    slidesToScroll: 3,
    // centerMode: true,
    arrows: true,
    infinite: false,
  };

  public showKeyboard: boolean = true;
  public form: FormGroup;
  public email = '';
  public deactivateActivateButtons: string = 'prev';
  public activeSlide: any;
  public activeClose:boolean = false;

  public isValid:boolean = false
  public isChecking: boolean = false;
  public validationStatus: string = '';
  constructor(public fb: FormBuilder,
              private router: Router,
              private brokerService:BrokerService,
              private clientService:ClientService,
              private photoService:PhotoService) {
   }

  ngOnInit(): void {
    ActivityMode.startTimer();
    this.slides = this.clientService.photoToCart.map(
      (item:any)=>{
        item.isCheckedOnEmail = false
        item.sumOfPaper = 0;
        item.totalPrice = this.brokerService.priceForEmail;
        item.totalCount = 1;
        item.sumOfEmail = 1;
        item.thumbUri = typeof item.thumbUri  == 'object' ? item.thumbUri.changingThisBreaksApplicationSecurity : item.originalUri;
        return item;
      });
    this.photoService.photo = this.slides;
    this.sumCount = this.slides.length * this.brokerService.priceForEmail;
    this.brokerService.sumForPay = this.sumCount;    
  }

  public onSelectItem(slide: any): any {
    if (!this.activeSlide) {
      this.activeSlide = slide;
      if (this.activeSlide.isCheckedOnEmail == true) {
        this.activeSlide.isCheckedOnEmail = false
      } else {
        this.activeSlide.isCheckedOnEmail = true;
      }
    } else {
      this.activeSlide.isCheckedOnEmail = false
      this.activeSlide = slide;
      if (this.activeSlide.isCheckedOnEmail == true) {
        this.activeSlide.isCheckedOnEmail = false
      } else {
        this.activeSlide.isCheckedOnEmail = true;
      }
    }
  }
  private isEmail(email: string): boolean {
    return /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/.test(email);
  }
  public checkValidationForm() {
    if (this.isEmail(this.email)) {
      this.isValid = true;
      this.validationStatus = 'success';
      return;
    }
    this.isValid = false;
    this.validationStatus = 'error';
  }

  public onChangeEmail(event: string) {
    this.email = event;
    this.checkValidationForm();
    console.log('event', event);
  }

  public deleteEl(slide:any,i:any):any{
    this.slides.splice(i,1);
    this.sumCount -= this.brokerService.priceForEmail;
    this.brokerService.sumForPay = this.sumCount;
  }

  goBack(){
    this.clientService.email = null;
    this.router.navigate(['howPrint'])
  }

  goHome(){
    this.clientService.email = null;
    this.router.navigate([''])
  }

  onCloseKeyboard(status: any) {
    if (status) {
      this.showKeyboard = false;
      this.checkValidationForm();
    }
  }

  goNext(){
    this.clientService.email = this.email;
    this.router.navigate(['paymentFormat']);
  }
}
